import { authAPI } from "../api/api";

const SET_USER_DATA = 'samurai-network/auth/SET-USER-DATA';


let initialState =  {
    userId: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    error: ""
};

const authReducer = (state = initialState, action) => {

    switch(action.type){
        case SET_USER_DATA:
            
            return {  
                ...state,
                ...action.payload,
                }
        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login, isAuth, error) => (
    { type: SET_USER_DATA, payload: { userId, email, login, isAuth, error } 
});

export const getAuthUserData = () => async dispatch => {
    let data = await authAPI.me()
        if(data.resultCode === 0){
            let {id, login, email} = data.data
            dispatch(setAuthUserData(id, email, login, true, ""))
        }
}
export const login =  (email, password, rememberMe, error) => async (dispatch) => {
    let data = await authAPI.login(email, password, rememberMe, error)
            if (data.resultCode === 0) {
                dispatch(getAuthUserData());
            } else {
                error = data.messages.length > 0 ? data.messages[0] : "";
                dispatch(setAuthUserData(null, null, null, false, error));
            }
}

export const logout = () => async (dispatch) => {
    let data = await authAPI.logout()
        if(data.resultCode === 0){
            dispatch(setAuthUserData(null, null, null, false, ""))
        }
}
export default authReducer;